<template>
  <div class="admin-login-step-1">
    <AuthTitle>
      {{ tp('adminLogin') }}
    </AuthTitle>
    <Form class="d-flex flex-column align-items-center">
      <Text
        mb="12px"
        :opacity="error ? 1 : 0"
        class="admin-login-step-1__error"
      >{{ tp('errorMessage') }}</Text>
      <Input
        v-model="form.uid.value"
        :placeholder="tp('uid')"
        hide-shadow
        bordered
        :error="form.uid.error"
        @update:model-value="form.uid.validate"
      >
        <template #prepend>
          <Icon name="user" />
        </template>
      </Input>
      <Input
        v-model="form.password.value"
        :placeholder="tp('password')"
        hide-shadow
        bordered
        type="password"
        :error="form.password.error"
        @update:model-value="form.password.validate"
      >
        <template #prepend>
          <Icon name="password" />
        </template>
      </Input>
      <Button type="primary" @click="onSubmit">
        <Text
          weight="400"
          size="14px"
          line-height="16px"
          color="white"
        >{{ tp('signInButton') }}</Text>
      </Button>
      <ButtonLink @click="onForgotPassword">
        <Text opacity="0.4">{{ tp('forgotPassword') }}?</Text>
      </ButtonLink>
    </Form>
  </div>
</template>

<script>
import { ROUTES_NAMES } from '@/admin/common/routes'
import { useI18n } from '@/shared/plugins/i18n'
import { reactive, ref } from 'vue'
import { useRouter } from 'vue-router'
import Text from '@/shared/components/UIKit/Text/Text'
import Form from '@/shared/components/Form/Form'
import Icon from '@/shared/components/Icon/Icon'
import Input from '@/shared/components/UIKit/Input'
import Button from '@/shared/components/UIKit/Button'
import AuthTitle from '@/admin/views/Auth/AuthTitle'
import ButtonLink from '@/shared/components/UIKit/ButtonLink'

export default {
  name: 'Step1',
  components: {
    ButtonLink,
    Text,
    Icon,
    Form,
    Input,
    Button,
    AuthTitle
  },
  emits: ['submit'],
  setup (_, { emit }) {
    const { tp } = useI18n('adminAuth.login')
    const router = useRouter()
    const error = ref(false)

    const form = reactive({
      uid: {
        value: '',
        error: false,
        validate: () => {
          form.uid.error = !form.uid.value.length
          return !form.uid.error
        }
      },
      password: {
        value: '',
        error: false,
        validate: () => {
          form.password.error = !form.password.value.length
          return !form.password.error
        }
      }
    })

    const onSubmit = () => {
      error.value = validate()
      if (error.value) {
        return
      }
      emit('submit', form)
    }
    const onForgotPassword = () => {
      router.push({
        name: ROUTES_NAMES.RESET_PASSWORD
      })
    }
    const validate = () => {
      form.uid.validate()
      form.password.validate()

      return form.password.error && form.uid.error
    }

    return {
      tp,
      form,
      error,
      onSubmit,
      onForgotPassword
    }
  }
}
</script>

