  padding: 40px 55px 0 55px;

  .admin-auth-title {
    margin-bottom: 30px;
  }
  .form {
    .input {
      width: 100%;
      padding: 14px 17px;
      margin-bottom: 17px;

      &:last-of-type {
        margin-bottom: 20px;
      }
    }
    .button {
      width: 100%;
    }
    .button-link {
      margin-top: 30px;
    }
  }
  &__error {
    color: $adminErrorColor;
  }
}
</style>
